import TruffleContract from 'truffle-contract';
import _ from 'lodash';
import DonationContract from '../contracts/Donation.json';
import { getProviderAndAccounts, sendTransaction, estimateGas } from '../helpers/web3Helpers';
import { getWeb3 } from "../helpers/getWeb3";

export async function donationContract() {
    const contract = TruffleContract(DonationContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function donatable() {
    const contract = await donationContract();
    return contract.donatable();
}

export async function donationInfo() {
    const contract = await donationContract();
    const web3 = await getWeb3();
    const datas = await contract.donationInfo();
    var info = {};
    info.currentDay = datas[0].toNumber();
    info.rate = datas[1].toNumber();
    info.remainTokenOfDay = web3.fromWei(datas[2].toNumber(), "ether");
    info.tokenOfDay = web3.fromWei(datas[3].toNumber(), "ether");
    info.ttLimit = web3.fromWei(datas[4].toNumber(), "ether");
    return info;
}

export async function donate(param) {
    const contract = await donationContract();
    const { accounts } = await getProviderAndAccounts();
    const web3 = await getWeb3();
    // 交易数据
    var transactionObject = {};
    transactionObject.from = accounts[0];
    transactionObject.to = contract.address;
    transactionObject.value = web3.toWei(param.ttAmount,'ether');
    return sendTransaction(transactionObject);
    
}