import TruffleContract from 'truffle-contract';
import { promisify } from 'util';
import _ from 'lodash';
import {getLang} from "@web3game/lang";

import ScrollDataContract from '../contracts/ScrollData.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';
import { getWeb3 } from "../helpers/getWeb3";

export async function scrollDataContract() {
    const contract = TruffleContract(ScrollDataContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function buyScrollEvent() {
    const contract = await scrollDataContract();
    const web3 = await getWeb3();
    const { accounts } = await getProviderAndAccounts();
    const event = contract.BuyScroll({_buyer: accounts[0]}, {fromBlock: 0, toBlock: 'latest'});
    const getAsync = promisify(event.get.bind(event));
    const logs = await getAsync();
    var events = [];
    if(logs.length === 0) {
        return events;
    }
    for(var i = 0; i < logs.length; i++) {
        const log = logs[i];
        var e = {};
        e.key = 'BuyScroll_' + i;
        e.from = log.args['_buyer'];
        e.amount = '-' + _.ceil(web3.fromWei(log.args['_total'].toNumber(), 'ether'), 3);
        e.time = log.args['_buyTime'].toNumber() * 1000;
        e.eventName = getLang().LOGS.GET_TASK + (log.args['_scrollType'].toNumber() + 1);
        events.push(e);
    }
    return events;
}

