import TruffleContract from 'truffle-contract';
import { promisify } from 'util';
import _ from 'lodash';
import InstitutionDataContract from '../contracts/InstitutionData.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';
import { getWeb3 } from "../helpers/getWeb3";
import {getLang} from "@web3game/lang";

export async function institutionDataContract() {
    const contract = TruffleContract(InstitutionDataContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function rewardEvent() {
    const contract = await institutionDataContract();
    const { accounts } = await getProviderAndAccounts();
    const web3 = await getWeb3();
    const event = contract.Reward({_to: accounts[0]}, {fromBlock: 0, toBlock: 'latest'});
    const getAsync = promisify(event.get.bind(event));
    const logs = await getAsync();
    var events = [];
    if(logs.length === 0) {
        return events;
    }
    for(var i = 0; i < logs.length; i++) {
        const log = logs[i];
        var e = {};
        var rewardType = log.args['_rewardType'].toNumber();
        var eventName = '';
        switch(rewardType) {
            case 0:
                eventName = getLang().LOGS.GAIN_STATIC_PROFIT;
                break;
            case 1:
                eventName = getLang().LOGS.GET_TASK_REWARD;
                break;
            case 2: 
                eventName = getLang().LOGS.GAIN_STATIC_PROFIT_REWARD;
                break;
            case 3:
                eventName = getLang().LOGS.POINT_OF_KP_BET;
                break;
            case 4:
                eventName = getLang().LOGS.POINT_OF_NN_BET;
                break;

        }
        e.key = 'IntroduceReward_' + i;
        e.from = log.args['_from'];
        e.amount = '+' + _.ceil(web3.fromWei(log.args['_reward'].toNumber(), 'ether'), 3);
        e.time = log.args['_rewardTime'].toNumber() * 1000;
        e.eventName = eventName;
        events.push(e);
    }
    return events;
}

export async function drawQuarterRewardEvent() {
    const contract = await institutionDataContract();
    const { accounts } = await getProviderAndAccounts();
    const web3 = await getWeb3();
    const event = contract.DrawQuarterReward({_drawer: accounts[0]}, {fromBlock: 0, toBlock: 'latest'});
    const getAsync = promisify(event.get.bind(event));
    const logs = await getAsync();
    var events = [];
    if(logs.length === 0) {
        return events;
    }
    for(var i = 0; i < logs.length; i++) {
        const log = logs[i];
        var e = {};
        e.key = 'DrawQuarterReward_' + i;
        e.from = log.args['_drawer'];
        e.amount = '+' + _.ceil(web3.fromWei(log.args['_reward'].toNumber(), 'ether'), 3);
        e.time = log.args['_drawTime'].toNumber() * 1000;
        e.eventName = getLang().LOGS.GAIN_QUARTER_REWARD;
        events.push(e);
    }
    return events;
}