import TruffleContract from 'truffle-contract';

import TokenContract from '../contracts/CupcakeToken.json';
import { getProviderAndAccounts, getAccounts, sendTransaction } from '../helpers/web3Helpers';
import { institutionContract } from './institutionContract';
import { getWeb3 } from "../helpers/getWeb3";

export async function tokenContract() {
    const contract = TruffleContract(TokenContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function getTokenBalance() {
    const accounts = await getAccounts();
    const contract = await tokenContract();
    return contract.balanceOf(accounts[0]);
}

export async function buyScroll(scrollType, price) {
    const contract = await tokenContract();
    const institution = await institutionContract();
    return contract.buyScroll(institution.address, scrollType, web3.toWei(price, 'ether'), 1, web3.toWei(price, 'ether'));
}

export async function bet(betAddress, betType, value, issueNo, nums, amounts) {
    const contract = await tokenContract();
    const institution = await institutionContract();
    return contract.bet(
        betAddress, 
        institution.address, 
        betType, 
        value, 
        issueNo, 
        nums, 
        amounts,
    );
}

export async function transfer(transferParam) {
    const web3 = await getWeb3();
    const { accounts } = await getProviderAndAccounts();
    if(transferParam.type === 'TT') {
        var transactionObject = {};
        transactionObject.from = accounts[0];
        transactionObject.to = transferParam.toAccount;
        transactionObject.value = web3.toWei(transferParam.amount,'ether');
        return sendTransaction(transactionObject);
    }
    const contract = await tokenContract();
    return contract.transfer(transferParam.toAccount, web3.toWei(transferParam.amount,'ether'));
}
