import TruffleContract from 'truffle-contract';
import { promisify } from 'util';
import _ from 'lodash';
import DonationDataContract from '../contracts/DonationData.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';
import { getWeb3 } from "../helpers/getWeb3";
import {getLang} from "@web3game/lang";

export async function donationDataContract() {
    const contract = TruffleContract(DonationDataContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function donateEvent() {
    const contract = await donationDataContract();
    const { accounts } = await getProviderAndAccounts();
    const web3 = await getWeb3();
    const event = contract.DonateEvent({_donor: accounts[0]}, {fromBlock: 0, toBlock: 'latest'});
    const getAsync = promisify(event.get.bind(event));
    const logs = await getAsync();
    var events = [];
    if(logs.length === 0) {
        return events;
    }
    for(var i = 0; i < logs.length; i++) {
        const log = logs[i];
        var e = {};
        e.key = 'Donation_' + i;
        e.from = log.args['_donor'];
        e.amount = '+' + _.ceil(web3.fromWei(log.args['_tokenAmount'].toNumber(), 'ether'), 3);
        e.time = log.args['_donateTime'].toNumber() * 1000;
        e.eventName = getLang().LOGS.DONATE +  _.ceil(web3.fromWei(log.args['_ttWeiAmount'].toNumber(), 'ether'), 3) + "TT";
        events.push(e);
    }
    return events;
}