import TruffleContract from 'truffle-contract';
import InstitutionContract from '../contracts/Institution.json';
import { getProviderAndAccounts } from '../helpers/web3Helpers';

export async function institutionContract() {
    const contract = TruffleContract(InstitutionContract);
    const { currentProvider, accounts } = await getProviderAndAccounts();
    contract.setProvider(currentProvider);
    contract.defaults({from: accounts[0]});
    return contract.deployed();
}

export async function batchDrawStaticProfit() {
    const contract = await institutionContract();
    return contract.batchDrawStaticProfit();
}

export async function releaseScroll(task) {
    const contract = await institutionContract();
    return contract.releaseScroll(task.scrollType, task.id);
}

export async function renewScroll(task) {
    const contract = await institutionContract();
    return contract.renewScroll(task.scrollType, task.id);
}


