import {
    InvitationInfo,
    MyBonusResponse,
    MyTaskResponse,
    TaskInfo,
    MyTaskInfo,
    TransferParam, 
    RankResponse, 
    RankInfo, 
    TradingLog, 
    DonateParam,
    DonationInfo,
} from "@web3game/typings/home";
import {HomeAPI} from "./HomeAPI";
import { myScrolls, getQuarterRewards, drawQuarterRewards, scrolls } from "../contractHelps/personalCenterContract";
import { transfer, buyScroll } from "../contractHelps/tokenContract";
import { batchDrawStaticProfit, releaseScroll, renewScroll } from "../contractHelps/institutionContract";
import { myIntroduceInfo, setInviteCode } from "../contractHelps/introduceContract";
import { achievementTopN, weekAchievementData } from "../contractHelps/achievementTopNDataContract";
import { betTopN, weekBetData } from "../contractHelps/betValueTopNData";
import { buyScrollEvent, releaseScrollEvent, renewScrollEvent } from "../contractHelps/scrollDataContract";
import { donateEvent } from "../contractHelps/donationDataContract";
import { rewardEvent, drawQuarterRewardEvent } from "../contractHelps/InstitutionDataContract";
import { openBoxRewardEvent, openBoxBetEvent } from "../contractHelps/openBoxContract";
import { compareByTime } from "../../common/utils/functions/sort";
import { donate, donationInfo, donatable } from "../contractHelps/donationContract";

export class HomeAPIReal implements HomeAPI {
    
    public async donatable(): Promise<boolean> {
        return donatable();
    }
    
    public async getDonationInfo(): Promise<DonationInfo> {
        const info = await donationInfo();
        return info as DonationInfo;
    }

    public async doDonate(params: DonateParam): Promise<void> {
        return donate(params);
    }

    public async getTradingLog(): Promise<TradingLog[]> {
        const buyScrollEvents = await buyScrollEvent();
        const releaseScrollEvents = await releaseScrollEvent();
        const renewScrollEvents = await renewScrollEvent();
        const donateEvents = await donateEvent();
        const rewardEvents = await rewardEvent();
        const drawQuarterRewardEvents = await drawQuarterRewardEvent();
        const openBoxRewardEvents = await openBoxRewardEvent();
        const openBoxBetEvents = await openBoxBetEvent();
        var events = [
            ...buyScrollEvents, 
            ...releaseScrollEvents, 
            ...renewScrollEvents, 
            ...donateEvents, 
            ...rewardEvents, 
            ...drawQuarterRewardEvents,
            ...openBoxRewardEvents,
            ...openBoxBetEvents,
        ];
        // 按时间排序
        events = events.sort(compareByTime());
        return events as TradingLog[];
    }

    public async getMyEarningLog(): Promise<TradingLog[]> {
        return [];
    }

    public async getSystemAnnouncement(): Promise<TradingLog[]> {
        return [];
    }

    releaseScroll(task: MyTaskInfo): Promise<void> {
        return releaseScroll(task);
    }

    renewScroll(task: MyTaskInfo): Promise<void> {
        return renewScroll(task);
    }

    async getTaskList(): Promise<TaskInfo[]> {
        return scrolls();

    }

    async getMyTaskList(): Promise<MyTaskInfo[]> {
        console.error(Error('not implementation'));
        return [];
    }

    public async transfer(params: TransferParam) {
        return transfer(params);
    }

    public async getInvitationResponse(): Promise<InvitationInfo> {
        const introduceInfo = await myIntroduceInfo();
        const result: InvitationInfo = {
            code: introduceInfo[0],
            myInviter: introduceInfo[1],
            invitationList: introduceInfo[2]
        };
        return result;
    }

    public async getMyBonusResponse(): Promise<MyBonusResponse> {
        return await getQuarterRewards() as MyBonusResponse;
    }

    public async getMyTaskResponse(): Promise<MyTaskResponse> {
        const result: MyTaskResponse = await myScrolls();
        return result;
    }

    public async setInvitor(invitor: string): Promise<void> {
        return setInviteCode(invitor);
    }

    public async gainTaskEarning(): Promise<void> {
        return batchDrawStaticProfit();
    }

    public async gainQuoterBonus(): Promise<void> {
        return drawQuarterRewards();
    }

    public async addTask(taskInfo: TaskInfo): Promise<void> {
        const scrollType = taskInfo.id;
        const price = taskInfo.tti;
        return buyScroll(scrollType, price);
    }

    public async getGamePlayRankList(): Promise<RankResponse> {
        const rankInfos: RankInfo[] = await betTopN();
        const rankInfo: RankInfo = await weekBetData();
        return {myRank: rankInfo, rankList: rankInfos} as RankResponse;
    }

    public async getRefererRankList(): Promise<RankResponse> {
        const rankInfos: RankInfo[] = await achievementTopN();
        const rankInfo: RankInfo = await weekAchievementData();
        return {myRank: rankInfo, rankList: rankInfos} as RankResponse;
    }


}

export default new HomeAPIReal();
